import SignUpForm, { STATE_LOGIN } from 'components/SignUpForm';
import React from 'react';
import { Card, Col, Row, Nav, NavItem, NavLink, } from 'reactstrap';

class SignUpPage extends React.Component {
  handleAuthState = authState => {
    if (authState === STATE_LOGIN) {
      this.props.history.push('/login');
    } else {
      this.props.history.push('/signup');
    }
  };

  handleLogoClick = () => {
    this.props.history.push('/');
  };

  render() {
    return (
      <Row
        style={{
          height: '100vh',
          justifyContent: 'center',
          // alignItems: 'center',
        }}>
        <Col md={12} lg={12}>
          <Card className="no-border no-bg">
              <Nav>
                <NavItem>
                  <NavLink href="https://bluetrux.com/">Home</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink href="https://bluetrux.com#about">About</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink href="https://bluetrux.com/support">Support</NavLink>
                </NavItem>
              </Nav>
          </Card>
        </Col>
        <Col md={6} lg={4}>
          <Card body className="signup-frm">
            <SignUpForm
              authState={this.props.authState}
              onChangeAuthState={this.handleAuthState}
              onLogoClick={this.handleLogoClick}
            />
          </Card>
        </Col>
      </Row>
    );
  }
}

export default SignUpPage;
