import { STATE_LOGIN, STATE_LOGGEDIN } from 'components/AuthForm';
import GAListener from 'components/GAListener';
import { EmptyLayout, LayoutRoute, MainLayout } from 'components/Layout';
import PageSpinner from 'components/PageSpinner';
import AuthPage from 'pages/AuthPage';
import SignUpPage from 'pages/SignUpPage';
import ResetPwdPage from 'pages/ResetPwdPage';
import React from 'react';
import componentQueries from 'react-component-queries';
import { Provider } from "./components/Context";
import { BrowserRouter, Redirect, Route, Switch } from 'react-router-dom';
import './styles/reduction.scss';
import AsyncStorage from '@callstack/async-storage';
import conf from "./api/config";

/** used */
const SetupPage = React.lazy(() => import('pages/SetupPage'));
const ProfilePage = React.lazy(() => import('pages/ProfilePage'));
const OwnersPage = React.lazy(() => import('pages/OwnersPage'));
const ClientsPage = React.lazy(() => import('pages/ClientsPage'));
const DriversPage = React.lazy(() => import('pages/DriversPage'));
const UsersPage = React.lazy(() => import('pages/UsersPage'));
const TrucksPage = React.lazy(() => import('pages/TrucksPage'));
const ImportTrucksPage = React.lazy(() => import('pages/ImportTrucksPage'));
const LoadsPage = React.lazy(() => import('pages/LoadsPage'));
const EditLoadPage = React.lazy(() => import('pages/EditLoadPage'));
const ExpenseGroupsPage = React.lazy(() => import('pages/ExpenseGroupsPage'));
const ExpensesPage = React.lazy(() => import('pages/ExpensesPage'));
const ExpenseDeductions = React.lazy(() => import('pages/ExpenseDeductions'));
const TruckExpensesPage = React.lazy(() => import('pages/TruckExpensesPage'));
const WeeklyReportsPage = React.lazy(() => import('pages/WeeklyReportsPage'));
const FactoringReportsPage = React.lazy(() => import('pages/FactoringReportsPage'));
const DriverEarningsPage = React.lazy(() => import('pages/DriverEarningsPage'));
const DispatcherEarningsPage = React.lazy(() => import('pages/DispatcherEarningsPage'));
const BillingPayPage = React.lazy(() => import('pages/BillingPayPage'));
const BillingAccountPage = React.lazy(() => import('pages/BillingAccountPage'));
const AdvancePage = React.lazy(() => import('pages/AdvancePage'));
const DashboardPage = React.lazy(() => import('pages/DashboardPage'));


const getBasename = () => {
  return `/${process.env.PUBLIC_URL.split('/').pop()}`;
};

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      user : {},
      is_logged_in : false,
    };
    AsyncStorage.getItem(conf.secret)
    .then((results) => {
      if( results !== null )
      {
        let obje_results = JSON.parse(results);
        this.setState({
          user : obje_results.user,
          is_logged_in : true,
        });
      }
      // console.log('from storage', results);
    })
    .catch((error) => {
      this.setState({
        user : {},
        is_logged_in : false,
      });
      console.log('from storage error', error);
    });
  }
  switchStateHandler = newState => {
    console.log('new state', newState);
    this.setState({
      user : newState.user,
      is_logged_in : newState.is_logged_in,
    });
  };


  render() {
    return (
      <BrowserRouter basename={getBasename()}>
        <GAListener>
        <Provider value={{
            state: this.state,
            switchStateHandler: e => this.switchStateHandler(e),
          }}>
          <Switch>
            {!this.state.is_logged_in && (
                <div>
                  <LayoutRoute
                    exact
                    path="/"
                    layout={EmptyLayout}
                    component={props => (
                      <AuthPage {...props} authState={STATE_LOGIN} />
                    )}
                    />
                    <LayoutRoute
                    exact
                    path="/try/bluetrux"
                    layout={EmptyLayout}
                    component={props => (
                      <SignUpPage {...props} authState={STATE_LOGIN} />
                    )}
                    />
                    <LayoutRoute
                    exact
                    path="/reset/password"
                    layout={EmptyLayout}
                    component={props => (
                      <ResetPwdPage {...props} authState={STATE_LOGIN} />
                    )}
                    />
                </div>
            )}
            {this.state.is_logged_in && (
              <MainLayout breakpoint={this.props.breakpoint}>
                <React.Suspense fallback={<PageSpinner />}>
                  {/* misc */}
                  <Route exact path="/" component={DashboardPage} />
                  <Route exact path="/co/setup" component={SetupPage} />
                  <Route exact path="/user/profile" component={ProfilePage} />
                  {/* people */}
                  <Route exact path="/manage-owners" component={OwnersPage} />
                  <Route exact path="/manage-drivers" component={DriversPage} />
                  <Route exact path="/manage-users" component={UsersPage} />
                  <Route exact path="/manage-clients" component={ClientsPage} />
                  {/* trucks */}
                  <Route exact path="/manage-trucks" component={TrucksPage} />
                  <Route exact path="/import-trucks" component={ImportTrucksPage} />
                  {/* loads */}
                  <Route exact path="/manage-loads" component={LoadsPage} />
                  <Route exact path="/manage-loads/edit/:id" component={EditLoadPage} />
                  {/* expenses */}
                  <Route exact path="/expenses-groups" component={ExpenseGroupsPage}/>
                  <Route exact path="/manage-expenses" component={ExpensesPage}/>
                  <Route exact path="/expense-deductions" component={ExpenseDeductions}/>
                  <Route exact path="/truck-expenses" component={TruckExpensesPage}/>
                  {/* reports */}
                  <Route exact path="/weekly-reports" component={WeeklyReportsPage}/>
                  <Route exact path="/factoring-reports" component={FactoringReportsPage}/>
                  {/* earnings */}
                  <Route exact path="/drivers-earnings" component={DriverEarningsPage}/>
                  <Route exact path="/dispatcher-earnings" component={DispatcherEarningsPage}/>
                  {/* advances */}
                  <Route exact path="/advance-payments" component={AdvancePage}/>
                  {/* billing */}
                  <Route exact path="/bills/billing" component={BillingAccountPage}/>
                  <Route exact path="/bills/payment" component={BillingPayPage}/>
                </React.Suspense>
              </MainLayout>
              // <Redirect to="/" />
            )}
          </Switch>
        </Provider>
        </GAListener>
      </BrowserRouter>
    );
  }
}

const query = ({ width }) => {
  if (width < 575) {
    return { breakpoint: 'xs' };
  }

  if (576 < width && width < 767) {
    return { breakpoint: 'sm' };
  }

  if (768 < width && width < 991) {
    return { breakpoint: 'md' };
  }

  if (992 < width && width < 1199) {
    return { breakpoint: 'lg' };
  }

  if (width > 1200) {
    return { breakpoint: 'xl' };
  }

  return { breakpoint: 'xs' };
};

export default componentQueries(query)(App);
