import conf from "./config";

const requestReset = (email) => {
  return fetch(conf.base_api + "users/request/reset/" + email, {
    method: "POST",
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
    },
  })
  .then((res) => {
    return res.json();
  })
  .catch((error) => {
    return Promise.reject(error);
  });
};
