import logo200Image from 'assets/img/logo/logo_200.png';
import PropTypes from 'prop-types';
import SourceLink from 'components/SourceLink';
import React from 'react';
import AsyncStorage from '@callstack/async-storage';
import { Button, Form, FormGroup, Input, Label, Alert,Row,Col,Card,CardHeader, } from 'reactstrap';
import { requestReset, verifyReset, finishReset, } from "../api/api";
import PageSpinner from 'components/PageSpinner';
import conf from "../api/config";
import { Consumer } from "../components/Context";

class ResetPwdForm extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      email:'',
      code:'',
      password:'',
      c_password:'',
      showMail: true,
      showCode: false,
      showPwd: false,
      preload:false,
      error:'',
      msg:'',
    };
    this.handleSubmitEmail = this.handleSubmitEmail.bind(this);
    this.handleSubmitCode = this.handleSubmitCode.bind(this);
    this.handleSubmitPwd = this.handleSubmitPwd.bind(this);

    this.handleEmailInput = this.handleEmailInput.bind(this);
    this.handlePassInput = this.handlePassInput.bind(this);
    this.handleCPassInput = this.handleCPassInput.bind(this);
    this.handleCodeInput = this.handleCodeInput.bind(this);
    this.isValidEmailAddress = this.isValidEmailAddress.bind(this);
  }

  isValidEmailAddress = (email) => {
    if (/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {
        return true;
    }
    return false;
  }
  handleEmailInput = (event) => {
    const v = event.target.value;
    this.setState({...this.state, email: v,});
  };
  handlePassInput = (event) => {
    const v = event.target.value;
    this.setState({...this.state, password: v,});
  };
  handleCPassInput = (event) => {
    const v = event.target.value;
    this.setState({...this.state, c_password: v,});
  };
  handleCodeInput = (event) => {
    const v = event.target.value;
    this.setState({...this.state, code: v,});
  };

  handleSubmitEmail = () => {
    if(this.state.email.length < 4)
    {
        return;
    }
    this.setState({...this.state, preload:true,});
    requestReset(this.state.email).then((response) => {
        if( response.status != 200 )
        {
            this.setState({...this.state, preload:false, error:response.message, msg:''});
            return;
        }
        this.setState({
            ...this.state, 
            preload:false, 
            showCode:true,
            showMail:false,
            msg:response.message, 
            error:''
        });
    })
    .catch((error) => {
        this.setState({
            ...this.state, 
            preload:false, 
            msg:'', 
            error:'Connection error occured'
        });
        return;
    });
  };
  handleSubmitCode = () => {
    if(this.state.email.length < 4 || this.state.code.length < 6)
    {
        return;
    }
    this.setState({...this.state, preload:true,});
    verifyReset(this.state.code, this.state.email).then((response) => {
        if( response.status != 200 )
        {
            this.setState({...this.state, preload:false, error:response.message, msg:''});
            return;
        }
        this.setState({
            ...this.state, 
            preload:false, 
            showCode:false,
            showMail:false,
            showPwd:true,
            msg:response.message, 
            error:''
        });
    })
    .catch((error) => {
        this.setState({
            ...this.state, 
            preload:false, 
            msg:'', 
            error:'Connection error occured'
        });
        return;
    });
  };
  handleSubmitPwd = () => {
    this.setState({...this.state, preload:true,});
    let postData = {
        password: this.state.password, 
        c_password: this.state.c_password,
        email: this.state.email,
    };
    finishReset(postData).then((response) => {
        if( response.status != 200 )
        {
            this.setState({...this.state, preload:false, error:response.message, msg:''});
            return;
        }
        this.setState({
            ...this.state, 
            preload:false, 
            showCode:false,
            showMail:false,
            showPwd:true,
            msg:response.message, 
            error:''
        });
        setTimeout( () => {
            window.location.href = '/'; /** go to login */
        }, 2000);
        return;
    })
    .catch((error) => {
        this.setState({
            ...this.state, 
            preload:false, 
            msg:'', 
            error:'Connection error occured'
        });
        return;
    });
  };
  render() {
    const {
      showLogo,
      usernameLabel,
      usernameInputProps,
      passwordLabel,
      passwordInputProps,
      confirmPasswordLabel,
      confirmPasswordInputProps,
      children,
      onLogoClick,
    } = this.props;

    return (
      <Consumer>
        {context => (
          <Form>
            {showLogo && (
              <div className="text-center pb-4">
                <img
                  src={logo200Image}
                  className="rounded"
                  style={{ width: 60, height: 60, cursor: 'pointer' }}
                  alt="logo"
                  onClick={onLogoClick}
                />
              </div>
            )}
            {this.state.preload && (
              <Row>
              <Col xl={12} lg={12} md={12}>
                <Card>
                  <CardHeader><PageSpinner/></CardHeader>
                </Card>
              </Col>
            </Row>
            )}
            {this.state.error.length > 0 && (
              <Alert color="danger">{this.state.error}</Alert>
            )}
            {this.state.msg.length > 0 && (
              <Alert color="success">{this.state.msg}</Alert>
            )}
            {/* email */}
            {this.state.showMail && (
                <FormGroup>
                    <Label for={"Email Address"}>{"Email Address"}</Label>
                    <Input className="long-input" value={this.state.email} onChange={this.handleEmailInput} {...usernameInputProps}  />
                </FormGroup>
            )}
            {/* code */}
            {this.state.showCode && (
                <FormGroup>
                    <Label for={"Verification code"}>{"Verification code"}</Label>
                    <Input type="number" className="long-input" value={this.state.code} onChange={this.handleCodeInput} {...usernameInputProps}  />
                </FormGroup>
            )}
            {/* passwords */}
            { this.state.showPwd && (
                <div>
                    <FormGroup>
                        <Label for={"Set password"}>{"Set new password"}</Label>
                        <Input type="password" className="long-input" value={this.state.password} onChange={this.handlePassInput} />
                    </FormGroup>
                    <FormGroup>
                        <Label for={"Confirm new password"}>{"Confirm new password"}</Label>
                        <Input type="password" className="long-input" value={this.state.c_password} onChange={this.handleCPassInput} />
                    </FormGroup>
                </div>
            )}
            {this.state.showMail && (
                <div>
                    <hr />
                    <Button onClick={this.handleSubmitEmail} size="lg" className="bg-gradient-theme-left border-0" block > Request reset </Button>
                </div>
            )}
            {this.state.showCode && (
                <div>
                    <hr />
                    <Button onClick={this.handleSubmitCode} size="lg" className="bg-gradient-theme-left border-0" block > Verify now </Button>
                </div>
            )}
            {this.state.showPwd && (
                <div>
                    <hr />
                    <Button onClick={this.handleSubmitPwd} size="lg" className="bg-gradient-theme-left border-0" block > Reset Password </Button>
                </div>
            )}
            <br></br>
            {children}
            <FormGroup>
              <Label>
                <SourceLink target="_self" href="/"> {" "}Go to Sign In</SourceLink>
              </Label>
            </FormGroup>
          </Form>
        )}
      </Consumer>
    );
  }
}

export const STATE_LOGIN = 'LOGIN';
export const STATE_LOGGEDIN = 'LOGGEDIN';

ResetPwdForm.propTypes = {
  authState: PropTypes.oneOf([STATE_LOGIN, STATE_LOGGEDIN]).isRequired,
  showLogo: PropTypes.bool,
  usernameLabel: PropTypes.string,
  usernameInputProps: PropTypes.object,
  passwordLabel: PropTypes.string,
  passwordInputProps: PropTypes.object,
  confirmPasswordLabel: PropTypes.string,
  confirmPasswordInputProps: PropTypes.object,
  onLogoClick: PropTypes.func,
};

ResetPwdForm.defaultProps = {
  authState: 'LOGIN',
  showLogo: true,
  usernameLabel: 'Email',
  usernameInputProps: {
    type: 'email',
    placeholder: '',
  },
  passwordLabel: 'Password',
  passwordInputProps: {
    type: 'password',
    placeholder: '',
  },
  confirmPasswordLabel: 'Confirm Password',
  confirmPasswordInputProps: {
    type: 'password',
    placeholder: '',
  },
  onLogoClick: () => {},
};

export default ResetPwdForm;
